import {useContext} from "react";
import {ShareItemsContext} from "../contextAPI/shareItemsContext";

export const ShareItemsFormHeader = () => {
    const {step} = useContext(ShareItemsContext);

    const warningTextSwitch = (id) => {
        switch(id) {
            case 1:
                return "Uzupenij szczegy dotyczce Twoich rzeczy. Dziki temu bdziemy wiedzie komu najlepiej je przekaza.";
            case 2:
                return "Wszystkie rzeczy do oddania zapakuj w 60l worki. Dokadn instrukcj jak poprawnie spakowa rzeczy znajdziesz TUTAJ.";
            case 3:
                return "Jeli wiesz komu chcesz pomc, moesz wpisa nazw tej organizacji w wyszukiwarce. Moesz te filtrowa organizacje po ich lokalizacji bd celu ich pomocy.";
            case 4:
                return "Podaj adres oraz termin odbioru rzeczy.";
            default:
                return;
        }
    }

    return (<>
        {step<5 &&
        <section className="shareFormHeader">
            <div className="wrapper">
                <div className="content">
                    <h2>Wane!</h2>
                    <span>{warningTextSwitch(step)}</span>
                </div>
            </div>
        </section>
        }
        </>
    )
}